* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul,
li,
address,
p {
  margin: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button{
  border: none;
  background: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
a {
  text-decoration: none;
  transition: all 0.3s;
}
