* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul,
li,
address,
p {
  margin: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  background: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  transition: all 0.3s;
}

body {
  font-family: "Inter", sans-serif;
  overflow-x: hidden;
}

.pb-50 {
  padding-bottom: 50px;
}

.pb-60 {
  padding-bottom: 60px;
}

.mb--30 {
  margin-bottom: 30px;
}

.nav-container {
  padding: 0 100px;
}
@media (max-width: 1200px) {
  .nav-container {
    padding: 0 16px;
  }
}

.navbar {
  padding: 0;
}
@media (max-width: 1200px) {
  .navbar {
    padding: 16px 0;
  }
}

.navbar .navbar-nav .nav-item:hover .nav-link {
  color: #384bff;
}

/*----- For Desktop -------*/
@media screen and (min-width: 1199.5px) {
