  }
  .header-primary.navbar-transparent.affix .navbar-right .navbar-btn-links {
    color: #232323;
  }
  .header-primary.navbar-transparent.affix .navbar-right .currency-list {
    color: #232323;
  }
  .header-primary.navbar-transparent.affix .navbar-toggler span {
    background-color: #384bff;
  }
  .header-primary.navbar-transparent.affix .navbar-toggler::after, .header-primary.navbar-transparent.affix .navbar-toggler::before {
    background-color: #384bff;
  }
  .header-primary.navbar-transparent.navbar-black .navbar .navbar-nav .nav-link {
    color: #232323;
  }
  .header-primary.navbar-transparent.navbar-black .navbar .navbar-nav .nav-link::after {
    color: #232323;
  }
  .header-primary.navbar-transparent.navbar-black .navbar-right .navbar-btn-links {
    color: #232323;
  }
  .header-primary.navbar-transparent.navbar-black .navbar-right .currency-list {
    font-size: 14px;
    color: #232323;
  }
  .header-primary.navbar-transparent .navbar .navbar-nav .nav-link {
    color: #ffffff;
  }
  .header-primary.navbar-transparent .navbar .navbar-nav .nav-link::after {
    display: none;
  }
  .header-primary.navbar-transparent .nav-item:hover .nav-link::after {
    transform: rotate(-180deg);
    color: #ffffff;
  }
  .header-primary.navbar-transparent .navbar-toggler {
    width: 38px;
    height: 38px;
    padding: 0;
    box-shadow: none;
    position: relative;
    right: 0;
    z-index: 99;
    border: none;
  }
  .header-primary.navbar-transparent .navbar-toggler span {
    position: absolute;
    left: 9px;
    overflow: hidden;
    width: 26px;
    height: 2px;
    margin-top: -1px;
    text-indent: 200%;
    -webkit-transition: opacity 0.25s;
    transition: opacity 0.25s;
    background-color: #ffffff;
  }
  .header-primary.navbar-transparent .navbar-toggler::after, .header-primary.navbar-transparent .navbar-toggler::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 9px;
    width: 26px;
    height: 2px;
    pointer-events: none;
    transition: transform 0.25s;
    transform-origin: 50% 50%;
    background-color: #ffffff;
  }
  .header-primary.navbar-transparent .navbar-right .navbar-btn-links {
    color: #ffffff;
  }
  .header-primary.navbar-transparent .navbar-right .currency-list {
    font-size: 14px;
    color: #ffffff;
  }
  .header-primary .navbar .navbar-nav .nav-link {
    font-size: 16px;
    letter-spacing: -0.2px;
    line-height: initial;
    color: #232323;
    font-weight: 500;
    padding: 35px 0;
    margin: 0 26px;
    position: relative;
  }
  .navbar .dropdown-menu {
    z-index: 20;
    background-color: #ffffff;
    display: block;
    right: 0;
    left: 0;
    padding: 10px;
    border: none;
    top: 100%;
    visibility: hidden;
    transform: scale(1, 0);
    opacity: 0;
    min-width: 200px;
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 50px 100px rgba(0, 0, 0, 0.12);
    margin: 0;
    transform-origin: 0 0;
    transition: all 0.3s ease-in-out;
  }
  .navbar .dropdown-menu.two-column {
    min-width: 350px;
    border-radius: 0 0 10px 10px;
    padding: 25px 20px 20px 25px;
    box-shadow: none;
    background-color: #000000;
  }
  .navbar .dropdown-menu.two-column .shape {
    position: absolute;
    top: -15px;
    left: 50px;
  }
  .navbar .dropdown-menu.two-column .shape svg {
    color: #232323;
  }
  .navbar .dropdown-menu.two-column .dropdown-item {
    font-size: 14px;
    position: relative;
  }
  .navbar .dropdown-menu.two-column .dropdown-item:hover {
    color: #fff;
  }
  .navbar .dropdown-menu.two-column .dropdown-item .flip-item::after {
    background-color: #ffffff;
  }
  .navbar .dropdown-menu.two-column .dropdown-item .flip-item:hover::after {
    width: 100%;
    transition: width 0.3s ease;
    left: 0;
  }
  .navbar .dropdown-menu.two-column .dropdown-item .flip-item:hover span::after {
    color: #ffffff;
  }
  .navbar .dropdown-menu.two-column .dropdown-item .flip-item span::before, .navbar .dropdown-menu.two-column .dropdown-item .flip-item span::after {
    color: #ababab;
  }
  .navbar .dropdown-menu.two-column .dropdown-item .flip-item span::after {
    color: #ffffff;
  }
  .navbar .dropdown-menu .dropdown-menu {
    left: calc(100% + 10px);
    top: 0;
    right: auto;
    min-width: 240px;
    box-shadow: 0 20px 30px -10px rgba(0, 0, 0, 0.15);
    transform: scale(0, 1);
  }
  .navbar .dropdown:hover > .dropdown-menu {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
  .navbar .navbar-nav .nav-item.dropdown:hover .nav-link.dropdown-toggle {
    transition: all 0.4s ease-in-out;
  }
  .navbar .navbar-nav .nav-item.dropdown:hover .nav-link.dropdown-toggle::after {
    transform: rotate(180deg);
    color: #384bff;
  }
}
.navbar .navbar-nav .nav-link {
  font-size: 16px;
  letter-spacing: -0.2px;
  line-height: initial;
  color: #02274a;
  padding: 15px 0;
  margin: 0 26px;
  position: relative;
}
.navbar .navbar-nav .nav-link.flame::before {
  color: #fb2b69;
  content: "\f7e4";
  position: absolute;
  right: -5px;
  top: 30%;
  font-size: 12px;
  font-weight: 900;
  font-family: "Font Awesome 6 Free";
  animation: updown 2s ease infinite;
}
@keyframes updown {
  0% {
    transform: translateY(-40%);
  }
  50% {
    transform: translateY(40%);
  }
  100% {
    transform: translateY(-40%);
  }
}

.navbar .dropdown-menu .dropdown-item {
  font-size: 14px;
  padding: 0 15px;
  letter-spacing: 0px;
  text-transform: capitalize;
  line-height: 30px;
  color: #161619;
  background-color: transparent;
  display: inline-block;
  position: relative;
  transition: all 0.3s ease-in-out;
  white-space: inherit;
  font-weight: 500;
}
.navbar .dropdown-menu .dropdown-item .flip-item {
  position: relative;
  display: inline-block;
  overflow: hidden;
  font-weight: 500;
}
.navbar .dropdown-menu .dropdown-item .flip-item::after {
  content: "";
  height: 1px;
  width: 0;
  background-color: #384bff;
  position: absolute;
  bottom: 0px;
  left: 100%;
  transition: all 0.3s ease-in-out;
}
.navbar .dropdown-menu .dropdown-item .flip-item:hover::after {
  width: 100%;
  transition: width 0.3s ease;
  left: 0;
}
.navbar .dropdown-menu .dropdown-item .flip-item:hover span {
  transform: translateY(-105%);
}
.navbar .dropdown-menu .dropdown-item .flip-item:hover span::before {
  transform: skewY(7deg);
  color: #161619;
}
.navbar .dropdown-menu .dropdown-item .flip-item:hover span::after {
  color: #384bff;
  transform: skewY(0);
}
.navbar .dropdown-menu .dropdown-item .flip-item span {
  display: inline-block;
  position: relative;
  z-index: 1;
  color: transparent;
  transition: transform 1.2s cubic-bezier(0.19, 1, 0.22, 1);
}
.navbar .dropdown-menu .dropdown-item .flip-item span::before, .navbar .dropdown-menu .dropdown-item .flip-item span::after {
  top: 0;
  transform: skewY(0);
  transform-origin: right bottom;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
  content: attr(data-text);
  display: block;
  position: absolute;
  color: #161619;
}
.navbar .dropdown-menu .dropdown-item .flip-item span::after {
  top: 105%;
  transform: skewY(7deg);
  transform-origin: left top;
  transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
  color: #384bff;
}

.navbar .dropdown-menu .dropdown-item .tag {
  position: absolute;
  background-color: var(--red-one);
  line-height: 15px;
  color: #384bff;
  font-size: 10px;
  letter-spacing: 1px;
  padding: 0 4px;
  text-transform: uppercase;
  top: 0;
  right: 0;
  font-style: normal;
}

.navbar .dropdown-menu .dropdown:hover > .dropdown-item,
.navbar .dropdown-menu .dropdown-item.active,
.navbar .dropdown-menu .dropdown-item:hover {
  color: #384bff;
}
.navbar .dropdown-menu .dropdown:hover > .dropdown-item::before,
.navbar .dropdown-menu .dropdown-item.active::before,
.navbar .dropdown-menu .dropdown-item:hover::before {
  transform: scale3d(1, 1, 1);
  transform-origin: 0% 50%;
  transition-timing-function: ease;
}

.navbar .dropdown-toggle::after {
  content: "\f0d7";
  font-family: "Font Awesome 6 Free";
  border: none !important;
  font-size: 12px;
  font-weight: 900;
  position: absolute;
  line-height: 28px;
  color: #232323;
  top: calc(50% - 14px);
  text-align: center;
  transition: all 0.3s ease;
}

.navbar .dropdown-submenu .dropdown-toggle::after {
  content: "\f0da";
  font-family: "Font Awesome 6 Free";
  display: block;
  font-weight: 900;
  right: 15px;
  top: 0;
  background-color: transparent !important;
  color: inherit;
  width: auto;
  height: auto;
  line-height: 40px;
  font-size: 13px;
  transition: transform 0.3s ease-in-out;
}

.navbar .dropdown-submenu:hover > .dropdown-toggle::after {
  transform: rotate(90deg);
}

.navbar-right {
  gap: 18px;
}
@media (max-width: 575.98px) {
  .navbar-right {
    gap: 10px;
  }
}
.navbar-right .navbar-right-features {
