  font-weight: 600;
  color: #232323;
}
@media (max-width: 575.98px) {
  .navbar-right .navbar-btn-links {
    display: none;
  }
}
.navbar-right .currency-list {
  font-size: 14px;
  font-weight: 700;
}
@media (max-width: 575.98px) {
  .navbar-right .header-button {
    display: none;
  }
}

/*=======================
  // template-search
========================*/
.template-search {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 480px;
  background-color: #FFFFFF;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
  transform: translate(0px, -100%) scale(0, 0);
  opacity: 0;
  visibility: hidden;
  z-index: 999999;
  transition: all 0.7s ease-in-out;
}

.template-search .menu {
  margin-top: 140px;
}
.template-search .menu ul {
  display: flex;
  align-items: center;
  gap: 40px;
  justify-content: center;
}
.template-search .menu ul li a {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 35px;
  text-align: center;
  color: #232323;
  transition: all 0.3s ease-in-out;
}
.template-search .menu ul li a:hover {
  color: #384bff;
}

.template-search .search-form {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

.template-search .search-form input[type=search] {
  width: 60%;
  color: #a1a1a1;
  font-size: 15px;
  text-align: left;
  border: none;
  margin: 0 auto;
  padding: 10px 0;
  outline: none;
  background-color: transparent;
  border-bottom: 1px solid #d9d9d9;
}
.template-search .search-form input[type=search]:focus {
  border-color: #232323;
}

.template-search .search-form .search-btn {
  margin-right: 20px;
  box-shadow: none;
  border: none;
  cursor: pointer;
  font-size: 15px;
  font-weight: 900;
  background-color: transparent;
  transition: all 0.3s ease-out;
}
.template-search .search-form .search-btn i {
  position: relative;
  right: 20px;
}

.template-search .search-form .search-btn:focus {
  box-shadow: none;
  outline: none;
}

.template-search .search-form input::-webkit-input-placeholder {
  color: #a1a1a1;
}

.template-search .search-form input::-moz-placeholder {
  color: #384bff;
}

.template-search .search-form input:-moz-placeholder {
  color: #384bff;
}

.template-search .search-form input:-ms-input-placeholder {
  color: #384bff;
}

.template-search .close {
  position: fixed;
  top: 50px;
  right: 100px;
  color: #232323;
  border: none;
  visibility: visible;
  font-size: 30px;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  background-color: #dedede;
  padding: 0px 20px;
}

.template-search .close:focus {
  box-shadow: none;
  border: none;
  outline: none;
}

.template-search .close:hover {
  opacity: 1;
}

.template-search.open {
  transform: translate(0px, 0px) scale(1, 1);
  opacity: 1;
  visibility: visible;
}

